import { EventEmitter } from '@angular/core';
import { Link } from './link';
import { Node } from './node';
import * as d3 from 'd3';
import GraphConfig from './graph-config';

const DEFAULT_CONFIG = {
  charge: -4000,
  xStrength: 1,
  yStrength: 1,
  linkDistance: 300,
  linkStrength: 0.5
}

export class NetworkGraph {
  public ticker: EventEmitter<d3.Simulation<Node, Link>> = new EventEmitter();
  public simulation: d3.Simulation<any, any>;

  public nodes: Node[] = [];
  public links: Link[] = [];

  config: GraphConfig;

  constructor(nodes, links, options: { width, height }, config?: GraphConfig) {
    this.nodes = nodes;
    this.links = links;

    this.config = config || DEFAULT_CONFIG;

    this.initSimulation(options);
  }

  connectNodes(source, target) {
    let link;

    if (!this.nodes[source] || !this.nodes[target]) {
      throw new Error('One of the nodes does not exist');
    }

    link = new Link(source, target);
    this.simulation.stop();
    this.links.push(link);
    this.simulation.alphaTarget(0.3).restart();

    this.initLinks();
  }

  initNodes() {
    if (!this.simulation) {
      throw new Error('simulation was not initialized yet');
    }

    this.simulation.nodes(this.nodes);
  }

  initLinks() {
    if (!this.simulation) {
      throw new Error('simulation was not initialized yet');
    }

    this.simulation.force('links',
      d3.forceLink(this.links)
        .id(d => d['id'])
        .distance(this.config.linkDistance)
        .strength(this.config.linkStrength)
    );
  }

  initSimulation(options) {
    if (!options || !options.width || !options.height) {
      throw new Error('missing options when initializing simulation');
    }

    /** Creating the simulation */
    if (!this.simulation) {
      const ticker = this.ticker;

      this.simulation = d3.forceSimulation()
        .force('charge',d3.forceManyBody().strength(this.config.charge))
        .force('center', d3.forceCenter(options.width / 2, options.height / 2))
        .force('x', d3.forceX(options.width / 2).strength(this.config.xStrength))
        .force('y', d3.forceY(options.height / 2).strength(this.config.yStrength));

      // Connecting the d3 ticker to an angular event emitter
      this.simulation.on('tick', function () {
        ticker.emit(this);
      });

      this.initNodes();
      this.initLinks();
    }

    /** Updating the central force of the simulation */
    this.simulation.force('centers', d3.forceCenter(options.width / 2, options.height / 2));

    /** Restarting the simulation internal timer */
    this.simulation.restart();
  }
}
